// Styling the icon requires extreme specificity
#wpadminbar #wp-admin-bar-iawp_admin_bar {

    > .ab-item {
        align-items: center;
        display: flex;

        .ab-icon:before {
            transition: none;
        }
    }
    &:hover,
    &:active,
    &:focus,
    &.hover {

        > .ab-item,
        .ab-icon:before {
            color: #f0f0f1;
        }
    }
} 
#wp-admin-bar-iawp_admin_bar {

    .ab-sub-wrapper .ab-item {
        padding: 0 14px;
    }
    .ab-submenu {
        padding: 8px 0 12px;
    }
    #wp-admin-bar-iawp_admin_bar_group_title {
        display: flex;
        justify-content: space-between;

        .ab-item span {
            font-weight: 700;
            color: #fff;
        }
        // Take up full space if i18n causes parent title to expand width of container
        > div {
            width: 100%;
        }
    }
    #wp-admin-bar-iawp_admin_bar-default {
        
        li div {
            display: flex;
            justify-content: space-between;
        }
    }
    #wp-admin-bar-iawp_admin_bar_dashboard_group {
        background: #1d2327;
    }
}

